import Fonts from "@root/assets/fonts/Fonts";

export default class Spanner extends React.Component {
  componentDidMount() {
    Fonts();
  }

  render() {
    return (
      <div className="spanner">
        <img src={require("@root/assets/images/" + this.props.imgSrc)}/>
        <div className="spanner-text">
          {this.props.children}
        </div>
        <div className="spanner-text-2">
          {this.props.secondaryText}
        </div>
        <style jsx>{`
          @font-face {
            font-family: 'Bhatoshine';
            font-weight: 300;
            src: url('/static/fonts/BhatoshineRegular.woff') format('woff');
          }

          @font-face {
            font-family: 'Brandon';
            font-weight: 600;
            src: url('/static/fonts/BrandonBold.otf') format('opentype');
          }

          .spanner {
            position: relative;
          }

          img {
            display: block;
            width: 100%;
          }

          .spanner-text {
            position: absolute;
            top: ${this.props.distance};
            width: 100%;
            text-align: center;
            text-transform: lowercase;
            font-family: 'Bhatoshine';
            font-size: 140px;
            color: ${this.props.color};
            z-index: 2;
          }

          .spanner-text-2 {
            position: absolute;
            top: ${this.props.secondaryDistance};
            width: 100%;
            text-align: center;
            text-transform: uppercase;
            font-family: 'Brandon';
            font-style: normal;
            font-weight: bold;
            font-size: 90px;
            letter-spacing: 0.05em;
            color: white;
            z-index: 1;
          }
        `}</style>
      </div>
    )
  }
}



