import FontFaceObserver from 'fontfaceobserver';

const Fonts = () => {
  const link = document.createElement('link');
  link.href = 'https://fonts.googleapis.com/css?family=Open+Sans|Sorts+Mill+Goudy:400,400i';
  link.rel = 'stylesheet';

  document.head.appendChild(link);

  const openSans = new FontFaceObserver('Open Sans');
  openSans.load().then(() => {
    document.documentElement.classList.add('openSans');
  })

  const goudy = new FontFaceObserver('Sorts Mill Goudy');
  goudy.load().then(() => {
    document.documentElement.classList.add('goudy');
  })
}

export default Fonts;



