import Head from "next/head";
import LinkBar from "@cmp/LinkBar";
import Logo from "@cmp/Logo";
import SocialPanel from "@cmp/SocialPanel";

export default class Header extends React.Component {
  render() {
    return (
      <div className="header">
        <Head>
          <title>Crystal Kustra | {this.props.title}</title>
          <meta charSet='utf-8' />
          <meta name='viewport' content='initial-scale=1.0, width=device-width' />
          <link rel="icon" type="image/x-icon" href="/static/images/favicon.ico" />
        </Head>
        <div className="div-header-left">
          <Logo />
        </div>
        <div className="div-header-right">
          <SocialPanel grey="true" />
          <LinkBar />
        </div>

        <style jsx>{`
          .header {
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-between;

            width: 100%;
            height: 200px;
            padding: 24px 0;
          }

          .div-header-left {
            width: 30%;
            padding-left: 40px;
          }

          .div-header-right {
            display: flex;
            flex-flow: column nowrap;
            justify-content: space-between;
            align-items: flex-end;

            width: 70%;
            padding-right: 40px;
          }
        `}</style>
      </div>
    );
  }
}



