import Constants from "@root/constants";
import Link from "next/link";

export default class SuccessStories extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      storyIndex: Math.floor(Math.random() * Constants.STORIES.length)
    };
  }

  changeStory = (newStoryIndex) => {
    document.querySelector('.story').classList.add('no-opacity');

    setTimeout(() => {
      this.setState({
        storyIndex: newStoryIndex
      });
      document.querySelector('.story').classList.remove('no-opacity');
    }, 700);
  }

  previousStory = () => {
    const newStoryIndex = (this.state.storyIndex + Constants.STORIES.length - 1) % Constants.STORIES.length;
    this.changeStory(newStoryIndex);
  }

  nextStory = () => {
    const newStoryIndex = (this.state.storyIndex + 1) % Constants.STORIES.length;
    this.changeStory(newStoryIndex);
  }

  render() {
    return (
      <div className="success">
        <div className="heading">Success Stories</div>
        <div className="main">
          <div className="arrow" onClick={this.previousStory}>&lt;</div>
          <div className="story">
            <div className="quote">{Constants.STORIES[this.state.storyIndex].quote}</div>
            <div className="speaker">- {Constants.STORIES[this.state.storyIndex].speaker}</div>
          </div>
          <div className="arrow" onClick={this.nextStory}>&gt;</div>
        </div>
        <Link prefetch href="/testimonials"><a className="more">More &gt;</a></Link>
        <style jsx>{`
          .success {
            display: flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            align-items: center;

            height: 320px;
            background-color: #3EB1C8;
          }

          .heading {
            font-size: 36px;
            text-transform: uppercase;
            color: #EEEEEE;
            font-weight: bold;
            letter-spacing: 0.05em;
            padding-top: 40px;
          }

          .main {
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            align-items: center;

            width: 65%;
            height: 100px;
            padding-top: 40px;
          }

          .arrow {
            font-size: 36px;
            font-weight: bold;
            color: #833177;
            cursor: pointer;
          }

          .story {
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;

            padding: 0 100px;
            text-align: center;
            color: #EEEEEE;
            font-size: 18px;

            opacity: 1;
            transition: opacity 0.7s ease-in;
          }

          .no-opacity {
            opacity: 0;
          }

          .speaker {
            align-self: flex-end;
            padding: 5px 40px 0;
          }

          .more {
            text-transform: uppercase;
            color: #833177;
            font-size: 22px;
            font-weight: bold;
            padding-top: 20px;
          }
        `}</style>
      </div>
    )
  }
}



