import facebook_grey from "@root/assets/images/facebook-grey.png";
import instagram_grey from "@root/assets/images/instagram-grey.png";
import linkedin_grey from "@root/assets/images/linkedin-grey.png";
import twitter_grey from "@root/assets/images/twitter-grey.png";

import facebook_white from "@root/assets/images/facebook-white.png";
import instagram_white from "@root/assets/images/instagram-white.png";
import linkedin_white from "@root/assets/images/linkedin-white.png";
import twitter_white from "@root/assets/images/twitter-white.png";

import facebook_teal from "@root/assets/images/facebook-teal.png";
import instagram_teal from "@root/assets/images/instagram-teal.png";
import linkedin_teal from "@root/assets/images/linkedin-teal.png";
import twitter_teal from "@root/assets/images/twitter-teal.png";

export default class SocialPanel extends React.Component {
  render() {
    return (
      <div className="panel">
        <a className="a-linkedin" href="https://www.linkedin.com/in/crystal-kustra-0155a254/">
          <div className="link linkedin" />
        </a>
        <a className="a-facebook" href="https://www.facebook.com/bamwellbeing/">
          <div className="link facebook" />
        </a>
        <a className="a-instagram" href="https://www.instagram.com/crystal.holistic.lifestyle/">
          <div className="link instagram" />
        </a>
        <style jsx>{`
          .panel {
            display: flex;
            flex-flow: row nowrap;

            height: 30px;
          }

          a {
            height: 100%;
            padding: 0 6px;
          }

          .a-linkedin {
            width: 28px;
          }

          .a-facebook {
            width: 15px;
          }

          .a-instagram {
            width: 28px;
          }

          .link {
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            height: 100%;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
          }

          .facebook {
            background-image: url(${this.props.grey ? facebook_grey : facebook_white});
          }

          .facebook:hover {
            background-image: url(${facebook_teal});
          }

          .instagram {
            background-image: url(${this.props.grey ? instagram_grey : instagram_white});
          }

          .instagram:hover {
            background-image: url(${instagram_teal});
          }

          .linkedin {
            background-image: url(${this.props.grey ? linkedin_grey : linkedin_white});
          }

          .linkedin:hover {
            background-image: url(${linkedin_teal});
          }
        `}</style>
      </div>
    )
  }
}



