import logo from "@root/assets/images/logo.png";
import logoWhite from "@root/assets/images/logo-white.png";

import Link from "next/link";

export default class Logo extends React.Component {
  render() {
    return (
      <div className="div-logo">
        <Link prefetch href="/">
          <img className="img-logo" src={this.props.white ? logoWhite : logo} />
        </Link>
        <style jsx>{`
          .div-logo {
            height: 100%;
          }

          .img-logo {
            height: 100%;
            width: 100%;
            object-fit: contain;
            object-position: 0 0;
            cursor: pointer;
          }
        `}</style>
      </div>
    );
  }
}



