import Definition from "@cmp/Definition";
import Fonts from "@root/assets/fonts/Fonts";
import Footer from "@cmp/Footer";
import Link from "next/link";
import Header from "@cmp/Header";
import Spanner from "@cmp/Spanner";
import SuccessStories from "@cmp/SuccessStories";

export default class Workshops extends React.Component {
  componentDidMount() {
    Fonts();
  }

  render() {
    return (
      <div>
        <Header title="Workshops" />
        <Spanner imgSrc="paradise.jpg" distance="40%" color="#833177">Workshops</Spanner>
        <div className="workshops-container">
          <div className="workshops-copy">
            Crystal can conduct workshops for your workplace, group or event.
            Offering inspiring information sessions customized to your audience.
            <br />
            Lunch’n’Learn sessions:
            <br />
            Mindfulness
            <br />
            Become Your Own Health Expert
            <br />
            BollyX
          </div>
          <Link href="/contact"><a className="contact">Contact Crystal</a></Link>
        </div>
        <Definition word="vitality" />
        <SuccessStories />
        <Footer />
        <style jsx global>{`
          body {
            margin: 0;
            font-family: 'Open Sans', sans-serif;
            min-width: 1360px;
          }

          a {
            text-decoration: none;
            color: inherit;
          }
        `}</style>
        <style jsx>{`
          .workshops-container {
            display: flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            align-items: center;

            color: grey;
          }

          .workshops-copy {
            padding: 40px 20% 30px 20%;
            font-size: 18px;
            line-height: 1.8em;
          }

          .contact {
            border: 1px solid #3EB1C8;
            padding: 4px 24px;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 20px;
            margin-bottom: 30px;
            color: #3EB1C8;
            text-decoration: none;
          }
        `}</style>
      </div>
    );
  }
}



