import Link from "next/link";

export default class LinkBar extends React.Component {
  render() {
    return (
      <div className="bar">
        <Link prefetch href="/"><a>Home</a></Link>
        <Link prefetch href="/about"><a>About</a></Link>
        <Link prefetch href="/coaching"><a>Holistic Lifestyle Coaching</a></Link>
        <Link prefetch href="/workshops"><a>Workshops</a></Link>
        <Link prefetch href="/resources"><a>Resources</a></Link>
        <Link prefetch href="/blog"><a>Blog</a></Link>
        <style jsx>{`
          .bar {
            display: flex;
            flex-flow: row nowrap;
            justify-content: flex-end;

            height: 30px;
          }

          a {
            text-decoration: none;
            text-transform: uppercase;
            color: grey;
            padding: 10px;
          }

          a:hover {
            color: #3EB1C8;
          }
        `}</style>
      </div>
    )
  }
}



