import background from "@root/assets/images/green-tree-bg.jpg";
import Fonts from '@root/assets/fonts/Fonts'

const WORDS = {
  wellness: {
    word: "Well•ness",
    partOfSpeech: "noun",
    definition: "The state of being in good health, especially as an actively pursued goal."
  },
  empower: {
    word: "Em•pow•er",
    partOfSpeech: "verb",
    definition: "Give someone authority or power to do something."
  },
  vitality: {
    word: "Vi•tal•i•ty",
    partOfSpeech: "noun",
    definition: "The state of being strong and active; energy."
  }
}

export default class Definition extends React.Component {
  componentDidMount() {
    Fonts();
  }

  render() {
    const word = WORDS[this.props.word];

    return (
      <div className="definition">
        <div className="def-word">{word.word}</div>
        <div className="def-part-of-speech">{word.partOfSpeech}</div>
        <div className="def-definition">{word.definition}</div>
        <style jsx>{`
          .definition {
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;
            align-items: center;

            background-image: url(${background});
            height: 250px;

            font-family: 'Sorts Mill Goudy', serif;
            letter-spacing: 0.1rem;
          }

          .def-word {
            font-size: 32px;
          }

          .def-part-of-speech {
            font-size: 24px;
            font-style: italic;
          }

          .def-definition {
            font-size: 24px;
          }
        `}</style>
      </div>
    )
  }
}



